.card {
    position: relative;
    height: 100%;
    width: 100%;
}
.card img {
    width: 100%;
    display: block;
    border: .2rem solid #fff;
    border-radius: .6rem;
}

/* front of card - the picture */

.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card - cover */

.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}